import React, {useRef} from 'react';
import emailjs from 'emailjs-com';

// import contact data
import { contact } from '../data';


const Contact = () => {

  const form = useRef();

  const sendEmail = (e) => {
    e.preventDefault();

    emailjs.sendForm('service_g7bpric', 'template_i1obdc9', form.current, 'tGwzLYVBpVHS_hgJ_')
      .then((result) => {
          console.log(result.text);
      }, (error) => {
          console.log(error.text);
      });

      e.target.reset();
  };

  return (
    <section className='section bg-teal-800' id='contact'>
      <div className='container mx-auto'>
        <div className='flex flex-col items-center text-center'>
          <h2 className='section-title before:content-contact relative before:absolute before:opacity-40 before:-top-7 before:-left-40 before:hidden before:lg:block'>
          Связаться с нами
          </h2>
          <p className='subtitle'>
            Мы обязательно ответим вам в ближайшее время
          </p>
        </div>
        <div
          className='flex flex-col lg:gap-x-8 lg:flex-row'
        >
          <div
            className='flex flex-1 flex-col items-start space-y-8 mb-12 lg:mb-0 lg:pt-2'
          >
            {contact.map((item, index) => {
              const { icon, title, subtitle, description } = item;
              return (
                <div className='flex flex-col lg:flex-row gap-x-4' key={index}>
                  <div className='text-teal-200 rounded-sm w-14 h-14 flex items-start justify-center mt-2 mb-4 lg:mb-0 text-2xl'>
                    {icon}
                  </div>
                  <div>
                    <h4 className='font-body text-xl mb-1 text-teal-200'>{title}</h4>
                    <p className='mb-1 text-paragraph text-white'>{subtitle}</p>
                    <p className='text-accent font-normal text-teal-200'>{description}</p>
                  </div>
                </div>
              );
            })}
          </div>
          <form ref={form} onSubmit={sendEmail}
            className='space-y-8 w-full max-w-[780px]'
          >
            <div className='flex gap-8'>
              <input className='input' name='name' type='text' placeholder='Ваше имя' />
              <input className='input' name='email' type='email' placeholder='Ваш email' />
            </div>
            <input className='input' name='uslugi' type='text' placeholder='Вид услуги' />
            <textarea
            name='message'
              className='textarea'
              placeholder='Ваше сообщение'
            ></textarea>
            <button type='submit' className='btn btn-lg bg-teal-400 hover:bg-teal-500'>
              Отправить сообщение
            </button>
          </form>
        </div>
      </div>
    </section>
  );
};

export default Contact;
